import { Component, OnInit } from '@angular/core';
import { RosCommunicationService } from '../../../commons/ros-communication.service';

@Component({
  selector: 'app-artificial-horizon',
  templateUrl: './artificial-horizon.component.html',
  styleUrls: ['./artificial-horizon.component.scss']
})
export class ArtificialHorizonComponent implements OnInit {

  constructor(private ros: RosCommunicationService) {
    ros.on('POSITION').subscribe(({ IMU }) => {
      // obróć wskazówkę #horizon_hand
      this.rotateToAngle(IMU[0]);
      // przesuń w pionie #horizon_scale
      this.moveScaleToAngle(IMU[1]);
    });
  }

  /**
   * Obraca element #horizon_hand na podany kąt.
   */
  rotateToAngle(deg) {
    let hand = document.getElementById("horizon_hand");
    hand.style.transform = 'rotate(' + deg + 'deg)';
  }

  /**
   * Przesuwa skalę pionową o kąt. Kąt przeliczany jest na piksele.
   * Trzeba wiedzieć ile pikseli na obrazku stanowi jeden stopień podziałki.
   * @param angle kąt w stopniach
   */
  moveScaleToAngle(angle) {
    var scale = document.getElementById("horizon_scale");
    // przy założeniu, że kreski skali są ustawione tak, że 1 stopień = 1.9px na obrazku
    var distance = 1.9 * angle;
    scale.style.transform = 'translateY(' + distance + ')';
  }

  ngOnInit() {
  }

}



