import { Component, OnInit } from '@angular/core';
import { RosCommunicationService } from '../../../commons/ros-communication.service';

@Component({
  selector: 'app-compass',
  templateUrl: './compass.component.html',
  styleUrls: ['./compass.component.scss']
})
export class CompassComponent implements OnInit {

  /** Element kompasu na stronie. */
  private compass;
  /** Poprzedni kąt kompasu. */
  private previousAngle: number = 0;

  constructor(private ros: RosCommunicationService) {
    ros.on('POSITION').subscribe(({ IMU }) => {
      this.rotateToAngle(IMU[2]);
    });
  }

  /**
   * Obraca element #compass na podany kąt.
   * TODO zamienić #compass na pole tej klasy?  @ViewChild
   * TODO BUG ta metoda wywołuje się kilka razy po zmianie ramię/jazda, najłatwiej sprawdzić dodając alert();
   * @param angleDeg kąt w stopniach
   */
  private rotateToAngle(angleDeg) {
    if (this.compass === undefined) {
      this.compass = document.getElementById("compass");
    }
    let correctedAngle = this.calculateCorrectAngle(angleDeg);
    this.compass.style.transform = 'rotate(' + -correctedAngle + 'deg)';
    this.previousAngle = correctedAngle;
  }

  /** Algorytm sprawdza czy bliżej jest obrócić się w prawo czy w lewo 
   *  i wylicza rrzeczywisty kąt na który trzeba obrócić kompas. Przykład:
   *  Bez przeliczania obrót z pozycji 350 stopni na 10 stopni odbędzie się w złym kierunku,
   *  czyli prawie dookoła, a wystarczy obrócić się w drugą stroną, bo to tylko o 20 stopni.
   * @param angleDeg kąt w stopniach
   */
  private calculateCorrectAngle(angleDeg) {
    let minDiff = Number.MAX_VALUE;
    let minDiffAngle = 0;
    if (angleDeg > this.previousAngle) {
      for (let i = angleDeg; i >= this.previousAngle - 360; i -= 360) {
        let diff = Math.abs(this.previousAngle - i);
        if (diff < minDiff) {
          minDiff = diff;
          minDiffAngle = i;
        }
      }
    } else {
      for (let i = angleDeg; i <= this.previousAngle + 360; i += 360) {
        let diff = Math.abs(this.previousAngle - i);
        if (diff < minDiff) {
          minDiff = diff;
          minDiffAngle = i;
        }
      }
    }
    return minDiffAngle;
  }

  ngOnInit() {
  }
}



